import React, { useState } from 'react';
import {Link} from "gatsby";
import {StaticImage} from 'gatsby-plugin-image';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMap, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { faInstagram, faYoutube } from '@fortawesome/free-brands-svg-icons';

import AnimatedVisibility from './AnimatedVisibility';
import MailForm from './MailForm';
import Map from './Map';
import './footerV2Style.scss';

const FooterV2 = () => {

    const [showMail, setShowMail] = useState(false);
    const [showMap, setShowMap] = useState(false);

    const showingMail = () => {
        if(showMail){
            setShowMail(false);

        } else{
            setShowMail(true);
            setShowMap(false);
        }
    }

    const showingMap = () => {
        if(showMap){
            setShowMap(false);

        } else{
            setShowMap(true);
            setShowMail(false);
        }
    }

    return (
        <div className='footerV2 row p-0 justify-content-center'>

            <ul className='col-12 col-sm-11 col-md-10 px-5 secciones d-flex flex-row justify-content-between'>
                <li><Link to="/nosotres">Nuestro equipo</Link></li>
                <li><Link to="/proyectos">Proyectos</Link></li>
                <li><Link to="/publicaciones">Publicaciones</Link></li>
                <li><Link to="/actividades">Actividades</Link></li>
            </ul>

            <div className='logo my-5'>
                <StaticImage 
                   src= "../images/logoit_it.png" 
                   alt="Logo Intercambios Transorganicos"
                   layout = "constrained"
                />
            </div>

            <ul className='col-10 redes d-flex flex-row justify-content-center'>
                <li className='m-3'><Link to="https://www.instagram.com/intercambios_transorganicos/"> <FontAwesomeIcon icon={faInstagram}/> </Link></li>
                <li onClick={showingMail} className='m-3'><FontAwesomeIcon icon={faEnvelope}/></li>
                <li onClick={showingMap} className='m-3'><FontAwesomeIcon icon={faMap}/></li>
                <li className='m-3'><Link to="https://www.youtube.com/channel/UC2ifoKIRdmPADvml5T212gA"> <FontAwesomeIcon icon={faYoutube}/> </Link></li>
            </ul>

            <div className='col-8 col-md-7 col-xl-6 my-3 linea-cuadro row p-0 justify-content-center'>
                <AnimatedVisibility visible={showMail}>
                    <MailForm/>
                </AnimatedVisibility>

                <AnimatedVisibility visible={showMap}>
                    <Map/>
                </AnimatedVisibility>
            </div>
            
            <div className='col-7 creditos'>
                <span>Es un proyecto de Gala Lucía Gonzalez Barrios.</span>
            </div>
        </div>
    )
}

export default FooterV2